export default {
    // components: { leafletMap, leafletBaseLayer },
    data() {
        return {
            name: "name",
            crs: L.CRS.EPSG4326,
            zoomControl: true,
            attributionControl: true,
            center: [36.982253, 106.1312172],
            // center: [37.8, -96],
            zoom: 6,
            minZoom: 1,
            maxZoom: 14,
        };
    },
    methods: {
        test() {
            let mapObject = this.$refs.leafletMap.getMap();
            let z = mapObject.getZoom();
            --z;
            mapObject.setZoom(z);
        },
        test2() {
            let mapObject = this.$refs.leafletMap.getMap();
            let z = mapObject.getZoom();
            ++z;
            mapObject.setZoom(z);
        }
    }
};