<template>
    <div class="">
        <div class="pad-6">
            <el-popover placement="right" trigger="click">
                <div>
                    <pre class="pre-detail-small">
    1、用法可参考本页面的案例
    2、支持leaflet的本身方法的调用，具体可参考leaflet的说明文档
       例如：let mapObject = this.$refs.leafletMap.getMap(); //可以获取map对象，
            // 使用 mapObject.XXX调用leaflet方法；
            // 例如：
            mapObject.setZoom(2);
    3、leaflet-map的属性参数说明：
        id: 字符串，选填，若一个页面需要展示多个地图，则必须传入多个id
        defaultStyle: 对象，选填，添加在leaflet容器的样式
        defaultClass: 字符串或对象，选填 多个class中间用空格
        剩下的leaflet的参数option的属性当做vue组件属性如数传入，比如{zoom:XX,minZoom:XX,maxZoom:XX,}等
    4、leaflet-base-layer的属性参数说明
        baseType: 数组 ，选填，若不传，默认显示矢量；
                  目前支持天地图的矢量，地形和 影像 ['skymapVec', 'skymapTopography', 'skymapImage']
                  传入数组 矢量skymapVec    地形skymapTopography    影像skymapImage
        showChangeBtn: 布尔类型，选填，是否显示切换地图的按钮，默认不显示
    5、获取底图的图层，然后可以用baseLayerObject调用leaflet原来的所有操作底图的方法
       例如：let baseLayerObject = this.$refs.leafletBaseLayer.getBaseLayer();
             // 使用 baseLayerObject.XXX调用leaflet方法；
                    </pre>
                </div>
                <el-button size="mini" slot="reference">查看用法详情</el-button>
            </el-popover>
            <el-button size="mini" @click="test">缩小</el-button>
            <el-button size="mini" @click="test2">放大</el-button>
        </div>
        <div class="" style="height:300px;">
            <leaflet-map ref="leafletMap" :zoom="4">
                <leaflet-base-layer ref="leafletBaseLayer" :showChangeBtn="true"
                    :baseType="['skymapVec', 'skymapTopography', 'skymapImage']">
                </leaflet-base-layer>
            </leaflet-map>
        </div>
    </div>
</template>

